import React, {Component} from 'react';
import Tree from "rc-tree";
import TreeNode from "rc-tree/lib/TreeNode";
import {Col} from "reactstrap";
import {searchTreeChildrenIdList} from "../../common/PeykHelper";

class AssigningAuthorityTree extends Component {

  constructor(props) {
    super(props);
    this.state = {
      selectedData: !!this.props.selectedData ? this.props.selectedData : {},
    };
  }

  onSelectionChange = (node, e) => {
    let selectedData = this.state.selectedData;
    let checked = e.target.checked;
    selectedData[node.key] = checked;
    let childrenList = searchTreeChildrenIdList(this.props.treeData[0], node.key);
    childrenList && childrenList.map(key => selectedData[key] = checked);
    this.setState({selectedData});
    this.props.onSelect(selectedData);
  }

  render() {
    let maxLength = this.state.maxLength ? this.state.maxLength : 0;
    const icon = ({}) => (<i className="fa fa-university"/>);
    const loop = (nodes, width) => {
      if (!nodes || !nodes.length) {
        return null
      }
      return nodes.map((node) =>
        <TreeNode key={node.key}
                  title={customLabel(node, width)}>
          {loop(node.children, width - 18)}
        </TreeNode>
      )
    };
    const customLabel = (node, width) => (
        <div style={{display: 'inline-flex'}}>
          <Col xs='10' style={{margin: '0', padding: '0', width: width + "px",}} title={node.title}>
            <label> {node.title}
              <input type="checkbox" style={{width: '40px'}}
                     checked={this.state.selectedData && this.state.selectedData[node.key]}
                     onChange={e => this.onSelectionChange(node, e)}/>
            </label>
          </Col>
        </div>
    );
    return (
      <div className="col-xl-auto">
          <Tree showLine
                icon={icon}
                selectable={false}
                defaultExpandedKeys={[this.props.treeData[0].key]}>
            {loop(this.props.treeData,maxLength)}
          </Tree>
      </div>
    );
  }
}

export default AssigningAuthorityTree;