import React, {Component} from "react";
import DetailedSearch from "../../../../components/DetailedSearch/DetailedSearch";
import Table from "../../../../components/Table/Table";
import {requestK} from "../../../../helper/APIUtils";
import {showAxiosError} from "../../../../helper/helper";
import MiniButton from "../../../../components/MiniButton";
import SweetAlertExt from "../../../../components/SweetAlertExt";
import Swal from "sweetalert2";
import CreateEmployeeGroup from "./CreateEmployeeGroup";

class EmployeeGroupCardPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            alert: null,
            content: [],
            loading: false,
            manual: false,
            searching: false,
            total: 0,
            page: 0,
            size: 10,
            totalIdList: [],
            searchQuery: undefined,
            displayCreateUpdateGroupDialog:false,
            selectedGroup:{}
        }
        this.fetchData = this.fetchData.bind(this);
        this.handleSearch = this.handleSearch.bind(this);
        this.deleteClicked = this.deleteClicked.bind(this);
        this.editClicked = this.editClicked.bind(this);
        this.closeCreateUpdateGroupDialog = this.closeCreateUpdateGroupDialog.bind(this);
        this.childRef = React.createRef();
    }

    closeCreateUpdateGroupDialog() {
        this.setState({displayCreateUpdateGroupDialog: false});
        this.handleSearch("");
    }

    editClicked(row) {
        this.setState({displayCreateUpdateGroupDialog: true, selectedGroup: row});
    }


    deleteClicked(row) {
        let self = this;
        let params = {
            url: '/employee/deleteGroup',
            method: "post",
            data: row
        };
        requestK(params)
            .then(function (response) {
                if (response.data === true) {
                    Swal({
                        text: 'Silme işlemi başarıyla gerçekleşti.',
                        type: 'success',
                        confirmButtonText: 'Tamam'
                    }).then((result) => {
                        self.handleSearch("");
                    });
                } else if (response.data === false) {
                    Swal({
                        text: 'Bu gruba bağlı çalışan vardır veya özlük formu kayıtları bulunmaktadır. Bu sebeple silme işlemi gerçekleştirilemedi.',
                        type: 'error',
                        confirmButtonText: 'Tamam'
                    }).then((result) => {
                    });
                }
            }).catch(function (error) {
            showAxiosError(error);
        });
    }

    handleSearch(searchQuery) {
        this.setState({
            searchQuery,
            searching: true
        }, () => this.fetchData(this.state.accessor, this.state.direction, 0, this.state.size))
    }

    fetchData(accessor, direction, page, size) {
        let self = this;
        let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '';
        let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
        let params = {
            url: '/employee/getGroupsPageList?page=' + page + '&size=' + size + searchQuery + sortStr,
            method: "get",
        };
        self.setState({loading: true, searching: true});
        requestK(params)
            .then(function (response) {
                self.setState({
                    page: page,
                    size: size,
                    total: response.data.totalElements,
                    content: response.data.content,
                    loading: false,
                    searching: false
                });
            }).catch(function (error) {
            self.setState({loading: false, searching: false});
            showAxiosError(error);
        });
    }

    render() {
        const fields = [
            {
                label: "Grup Adı",
                accessor: "name",
                type: "text",
                operator: "="
            }
        ];
        const columns = [
            {
                Header: "Grup Adı",
                accessor: "name",
                Cell: (item) => (
                    <div style={{textAlign: "left", width: '200px', overflow: 'auto'}}>{item.name}</div>)
            },
            {
                Header: "İşlemler",
                accessor: "",
                sortable: false,
                width: "auto",
                Cell: row => (
                    <div style={{textAlign: "center"}}>
                        <MiniButton title="Düzenle" faIcon="fa-edit" onClick={() => this.editClicked(row)}
                                    color="success"/>
                        <MiniButton title="Sil" faIcon="fa-trash" color="danger"
                                    onClick={() => {
                                        this.setState({
                                            alert: (
                                                <SweetAlertExt showCancel warning
                                                               confirmBtnText="Evet"
                                                               cancelBtnText="Hayır"
                                                               confirmBtnBsStyle="danger"
                                                               cancelBtnBsStyle="default"
                                                               title="Emin misiniz?"
                                                               disable={this.state.loading}
                                                               onConfirm={() => this.setState({alert: null}, () => {
                                                                   this.deleteClicked(row);
                                                               })}
                                                               onCancel={() => this.setState({alert: null})}
                                                >
                                                    {row.name + " isimli grup silinecek."}
                                                </SweetAlertExt>
                                            )
                                        });
                                    }}/>
                    </div>)

            }
        ];
        return (
            <div className="row">
                <div className="card-body">
                    <DetailedSearch fields={fields}
                                    loading={this.state.searching}
                                    onSearch={this.handleSearch}
                    />
                </div>
                <div className='col-sm-12'>
                    <Table columns={columns}
                           content={this.state.content}
                           loading={this.state.loading}
                           total={this.state.total}
                           page={this.state.page}
                           manual={this.state.manual}
                           size={this.state.size}
                           options={["5", "10", "20", "50", "100"]}
                           fetchData={this.fetchData}
                    />
                </div>
                {this.state.alert}
                {this.state.displayCreateUpdateGroupDialog &&
                    <CreateEmployeeGroup
                        selectedGroup={this.state.selectedGroup}
                        displayCreateUpdateGroupDialog={this.state.displayCreateUpdateGroupDialog}
                        closeModal={this.closeCreateUpdateGroupDialog}
                    />}
            </div>
        );
    }

}

export default EmployeeGroupCardPage;