import React, {Component} from 'react';
import DetailedSearch from "../../../../components/DetailedSearch/DetailedSearch";
import {requestK} from "../../../../helper/APIUtils";
import {checkGrantForFetchResponse, showAxiosError} from "../../../../helper/helper";
import DropdownCheck from "../../../../components/Table/DropdownCheck";
import TextOverFlowTooltip from "../../../../components/TextOverFlowTooltip";
import {
    cardTypeData,
    departmentData,
    genderData,
    renewListForPrivileges,
    statusTypeFilterData
} from "../../common/PeykHelper";
import MiniButton from "../../../../components/MiniButton";
import SweetAlertExt from "../../../../components/SweetAlertExt";
import Table from "../../../../components/Table/Table";
import VaccinationCardHistory from "./VaccinationCardHistory";
import moment from "moment";
import DisplaySelectionList from "../../../../helper/DisplaySelectionList";

class VaccinationCardPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selection: this.props.selection,
            selectedAll: false,
            content: [],
            selected: undefined,
            total: 0,
            page: 0,
            size: 10,
            loading: false,
            manual: false,
            searching: false,
            searchQuery: undefined,
            displayHistoryDialog: false,
            displaySelectionList: false
        };
        this.fetchData = this.fetchData.bind(this);
    }

    componentDidMount() {
        this.props.updateSelection(this.state.selection);
    }

    hasGrant = (grant) => {
        return this.props.hasGrant(grant);
    };

    isSelected(id) {
        return (this.state.selection.indexOf(id) !== -1);
    }

    handleSearch = (searchQuery) =>{
        this.setState({
            searchQuery,
            searching: true,
            // selection: [],
        }, () => this.fetchData(this.state.accessor, this.state.direction, 0, this.state.size))
    }

    fetchData(accessor, direction, page, size) {
        let self = this;
        let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '&sort=id,desc';
        let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
        let params = {
            url: '/employee/readVaccinationEmployeeAll?page=' + page + '&size=' + size + searchQuery+ sortStr +
                ((searchQuery !== '' && searchQuery.includes("manufacturer")) ? '' : '&manufacturer=') +
                ((searchQuery !== '' && searchQuery.includes("totalDoses")) ? '' : '&totalDoses=') +
                ((searchQuery !== '' && searchQuery.includes("doseNumber")) ? '' : '&doseNumber=') +
                ((searchQuery !== '' && searchQuery.includes("certificateId")) ? '' : '&certificateId=') +
                ((searchQuery !== '' && searchQuery.includes("cardType")) ? '' : '&cardType='),
            method: "post",
        };
        self.setState({loading: true, searching: true, manual: self.manual === true ? false : self.manual});
        self.props.refreshCompleted();
        requestK(params)
            .then(function (response) {
                checkGrantForFetchResponse(self, response.data);
                self.setState({
                    page: page,
                    size: size,
                    total: (response.data && response.data.page) ? response.data.page.totalElements : 0,
                    content: (response.data && response.data.page) ? response.data.page.content : [],
                    totalIdList: response.data ? response.data.selectedIdList : [],
                    loading: false,
                    searching: false
                });
            })
            .catch(function (error) {
                self.setState({loading: false, searching: false});
                showAxiosError(error);
            });
    }

    getTrStyles = (item) =>{
        return (item.statusType && item.statusType !== 'ACTIVE') ? {
            backgroundColor: 'lightgray',
            webkitTextFillColor: 'darkgray'
        } : {};
    }

    trigerModal = () => {
        this.setState({displaySelectionList: true});
    };

    toggleCheckboxChange(e, row, id) {
        let self = this;
        let checked = e.target.checked;
        let items = self.state.content;
        let selection = self.state.selection;
        if (row === "all") {
            let selection = [];
            if (checked) {
                items.forEach(item => {
                    selection.push(item[id]);
                });
            }
            self.setState({selection: selection, selectedAll: checked});
        } else {
            let index = selection.indexOf(row[id]);
            if (index >= 0) {
                selection = [
                    ...selection.slice(0, index),
                    ...selection.slice(index + 1)
                ];
            } else {
                selection.push(row[id]);
            }
            self.setState({
                selection: selection,
                selectedAll: ((selection.length > 0) ? (selection.length === self.state.content.length) : false)
            });
            self.props.updateSelection(selection);
        }
    }

    openHistoryDialog = (row)=> {
        let clone = Object.assign({}, row);
        delete clone["_$visited"];
        this.setState({
            selected: clone.id,
            valid: {},
            displayHistoryDialog: true
        });
    }

    closeHistoryDialog = ()=> {
        this.setState({
            selected: undefined,
            valid: {},
            displayHistoryDialog: false
        });
    }

    renderMainTable = () =>{
        let pageIdList = [];
        if (this.state.content && this.state.content instanceof Array) {
            this.state.content.map(e => {
                pageIdList.push(e.id);
            });
        }

        let columnFieldList = [
            {
                Header: (this.state.content &&
                    <DropdownCheck selectionIdList={this.state.selection}
                                   totalIdList={this.state.totalIdList}
                                   pageIdList={pageIdList}
                                   onChange={e => {
                                       this.setState({selection: e});
                                       this.props.updateSelection(e);
                                   }}/>
                ),
                accessor: "id",
                selectType: "checkbox",
                sortable: false,
                Cell: (row, index) => (this.state.content &&
                    <input type="checkbox" checked={this.isSelected(row.id)}
                           onChange={(e) => this.toggleCheckboxChange(e, row, "id", index)}/>)
            },
            {
                Header: "Ad",
                accessor: "name",
                Cell: row => (<div style={{margin: 0}}><TextOverFlowTooltip text={row.name} maxLength={12}/></div>)
            },
            {
                Header: "Soyad",
                accessor: "surname",
                Cell: row => (
                    <div style={{margin: 0}}><TextOverFlowTooltip text={row.surname} maxLength={12}/></div>)
            },
            {
                Header: "TCKN",
                accessor: "tckn"
            },
            {
                Header: "Departman",
                accessor: "department",
                Cell: row => (
                    <div style={{margin: 0}}>{row.department &&
                    <TextOverFlowTooltip text={row.department.name} maxLength={12}/>}</div>)
            },
            {
                Header: "Durum",
                accessor: "statusType",
                Cell: row => (
                    <div>
                        <span>{statusTypeFilterData().map(function (e) {
                            if (e.value === row.statusType) return e.label
                        })}</span>
                    </div>)
            },
            {
                Header: "Belge Türü",
                accessor: "vaccinationCards_cardType",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        <TextOverFlowTooltip text={row.vaccinationCard && row.vaccinationCard.cardType &&
                        row.vaccinationCard.cardType === "PCR" ? "PCR Testi": (row.vaccinationCard.cardType === "VACCINATION" ? "Aşı" : row.vaccinationCard.cardType) } maxLength={12}/>
                    </div>
                )
            },
            {
                Header: "Sertifika ID",
                accessor: "vaccinationCard_certificateId",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        <TextOverFlowTooltip text={row.vaccinationCard && row.vaccinationCard.certificateId} maxLength={12}/>
                    </div>
                )
            },
            {
                Header: "Test Açıklanma Tarihi",
                accessor: "vaccinationCards_resultDate",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        <TextOverFlowTooltip text={row.vaccinationCard && moment(row.vaccinationCard.resultDate).isValid() ? moment(row.vaccinationCard.resultDate).format("DD-MM-YYYY HH:mm:ss") : '' } maxLength={12}/>
                    </div>
                )
            },
            {
                Header: "Aşı Tarihi",
                accessor: "vaccinationCards_vaccinationDate",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        <TextOverFlowTooltip text={row.vaccinationCard && moment(row.vaccinationCard.vaccinationDate ).isValid() ? moment(row.vaccinationCard.vaccinationDate ).format("DD-MM-YYYY") : '' } maxLength={12}/>
                    </div>
                )
            },
            {
                Header: "Toplam Doz",
                accessor: "employee_vaccinationCards_totalDoses",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        <TextOverFlowTooltip text={row.vaccinationCard && row.vaccinationCard.totalDoses} maxLength={12}/>
                    </div>
                )
            },
            {
                Header: "Aşı Üreticisi",
                accessor: "vaccinationCard_manufacturer",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        <TextOverFlowTooltip text={row.vaccinationCard && row.vaccinationCard.manufacturer} maxLength={12}/>
                    </div>
                )
            },
            {
                Header: "İşlemler",
                accessor: "",
                sortable: false,
                Cell: row => {
                    return (
                        <div style={{textAlign: 'left', WebkitTextFillColor: 'white'}}>

                            <MiniButton title="Geçmiş Görüntüle" faIcon="fa-info"
                                        onClick={() => this.openHistoryDialog(row)}
                                        color="info"/>
                        </div>
                    )
                },
                width: 'auto'
            }
        ];
        // let columns = renewListForPrivileges(this, columnFieldList, "accessor", this.state.isKepOwner);
        let columns = columnFieldList;

        return <Table columns={columns}
                      content={this.state.content}
                      loading={this.state.loading}
                      loadingText="Kayıtlar getiriliyor. Lütfen Bekleyiniz.."
                      total={this.state.total}
                      page={this.state.page}
                      size={this.state.size}
                      manual={this.state.manual || this.props.manual}
                      options={["5", "10", "20", "50", "100", "500"]}
                      getTrStyles={this.getTrStyles}
                      fetchData={this.fetchData}/>
    }

    render() {
        const fields = [
            {
                label: "Adı",
                accessor: "name",
                type: "text",
                operator: "="
            },
            {
                label: "Soyadı",
                accessor: "surname",
                type: "text",
                operator: "="
            },
            {
                label: "TCKN",
                accessor: "tckn",
                type: "numericText",
                operator: "="
            },
            {
                label: "Departman",
                accessor: "department_name",
                type: "select",
                operator: "=",
                selectData: departmentData(this.props.departmentList),
            },
            {
                label: "Durum",
                accessor: "statusType",
                type: "select",
                operator: "=",
                selectData: statusTypeFilterData()
            },
            {
                label: "Belge Türü",
                accessor: "cardType",
                type: "select",
                operator: "=",
                selectData: cardTypeData()
            },
            // {
            //     label: "Aşılanma Tarihi (Başlangıç)",
            //     accessor: "min_vaccinationDate",
            //     type: "datetime",
            //     operator: ">=",
            // },
            // {
            //     label: "Aşılanma Tarihi (Bitiş)",
            //     accessor: "max_vaccinationDate",
            //     type: "datetime",
            //     operator: "<=",
            // },
            {
                label: "Toplam Doz",
                accessor: "totalDoses",
                type: "numericText",
                operator: "=",
            },
            {
                label: "Doz Numarası",
                accessor: "doseNumber",
                type: "numericText",
                operator: "=",
            },
            {
                label: "Aşı Üreticisi",
                accessor: "manufacturer",
                type: "text",
                operator: "="
            },
            {
                label: "Sertifika ID",
                accessor: "certificateId",
                type: "text",
                operator: "=",
            },
        ];
        return (
            <div className="row">
                <div className="card-body">
                    <DetailedSearch fields={fields}
                                    loading={this.state.searching}
                                    onSearch={this.handleSearch}/>
                </div>
                <div className="card-body">
                    <div className="row" style={{cursor: 'pointer', marginLeft: '10px', color: 'red'}}>
                        <span onClick={this.trigerModal}>
                           {(this.state.selection.length !== 0 ? "Seçilen çalışan sayısı " + this.state.selection.length : "")}
                        </span>
                    </div>
                    {this.renderMainTable()}
                </div>
                {this.state.alert}
                {this.state.displayHistoryDialog &&
                <VaccinationCardHistory selection={this.state.selected} modal={this.state.displayHistoryDialog}
                                        hasGrant={this.hasGrant}
                                        manual={this.state.manual}
                                        toggleModal={(flag, manual) => this.setState({displayHistoryDialog: flag, manual: manual})}/>}

                {this.state.displaySelectionList &&
                <DisplaySelectionList
                    selection={this.state.selection}
                    modal={this.state.displaySelectionList}
                    closeModal={() => this.setState({displaySelectionList: false})}
                    removeSelected={() => this.setState({selection : [], displaySelectionList: false},this.props.updateSelection([]))}
                />}
            </div>
        );
    }
}

export default VaccinationCardPage;