import React, {Component} from 'react';
import {Col, Form, FormGroup, Input, Label} from "reactstrap";
import {requestK} from "../../../../helper/APIUtils";
import {showAxiosError} from "../../../../helper/helper";
import Alert from "react-s-alert";
import AssigningAuthorityTree from "./AssigningAuthorityTree";
import DefaultModal from "../../../../components/DefaultModal";
import {authorizationWays} from "../../../../helper/helper";
class AssigningAuthority extends Component {
  constructor(props) {
    super(props);
    this.state = {
      btnDisabled: false,
      manual: false,
      employee: {},
      treeData: [],
      selectedData: {},
      existingAuthority: [],
      shouldRun: true,
      steps: [
        {
          target: '.card-title',
          content: "Bu Ekranda seçtiğiniz çalışana organizasyonel yetki verebilirsiniz.",
          placement: "center",
        },
        {
          target: 'table tbody tr:nth-child(1) td:nth-child(1) input',
          content: "Çalışana verilmiş organizasyonel yetkileri buradan kontrol edebilirsiniz.",
          placement: "auto",
        },
        {
          target: 'table tbody tr:nth-child(1) td:nth-child(1) input',
          content: "Yetki vermek istediğiniz organizasyon birimlerini seçiniz.",
          placement: "auto",
        },
        {
          target: '.kaydet',
          content: 'Kaydet',
          placement: "auto",
        },
      ],
      locale: {
        back: 'Geri',
        close: 'Kapat',
        last: 'Son',
        next: 'İleri',
        skip: 'Atla',
      },
    };
  }

  componentDidMount() {
    let self = this;
    let params = {
      url: `/employee/getEmployeeById`,
      method: "post",
      data: self.props.id ? self.props.id : -1
    };
    requestK(params)
      .then(function (response) {
        if (!response.data.id && self.props.tckn) {
          response.data.tckn = self.props.tckn;
        }
        self.setState({
          employee: response.data,
          manager: response.data.manager,
          department: AssigningAuthority.getDepartmentElement(response.data)
        });
      }).catch(function (error) {
      showAxiosError(error);
    });
    self.getExistingAuthority();
  }

  getExistingAuthority = () =>{
    let self = this;
    let params = {
      url: 'employee/getExistingAuthority',
      method: 'post',
      data: self.props.id
    };
    requestK(params)
      .then(function (response) {
        let list = response.data;
        let result = {};
        if (!!list && !!list.length) {
          list.map(id => {
            result[id.toString()] = true;
          });
        }
        self.setState({selectedData: result}, () => self.getData()); // burada selectedData ya verilerin aktarılmasını işlemi
      }).catch(function (error) {
      showAxiosError(error);
    });
  }

  getData = () => {
    let self = this;
    let params = {
      url: 'department/readDepartmentTreeData',
      method: "post",
      data: self.props.selectedAuthorizationWay
    };
    requestK(params)
      .then(function (response) {
        if (response.data) {
          self.setState({treeData: response.data});
        } else {
          Alert.error("Yetki verilecek birimler oluşturulmamış.", {
            position: 'top-right',
            effect: 'stackslide',
            timeout: 5000
          });
        }
      }).catch(function (error) {
      showAxiosError(error);
    });
  }

  static getDepartmentElement(data) {
    if (data.department) {
      return {value: data.department.id.toString(), label: data.department.name};
    } else {
      return null;
    }
  }

  renderInput(label, fieldName) {
    return <FormGroup row>
      <Label sm={4}>{label}</Label>
      <Col sm={8}>
        <Input type="text" name={fieldName}
               value={this.state.employee[fieldName] || ''}
        />
      </Col>
    </FormGroup>;
  }

  saveClicked = () => {
    let self = this;
    if (!!self.state.selectedData) {
      let params = {
        url: 'employee/saveOrganizationAuthority',
        method: "post",
        data: {
          "employeeId": self.props.id,
          "selectedList": self.readSelectedDeptIdList()
        },
      };
      requestK(params)
        .then(function () {
          Alert.success(self.props.selectedAuthorizationWay === authorizationWays.DEPARTMENT ? "Organizasyonel birimlere yetki kaydedildi." : "Masraf Yeri yetkisi kaydedildi.", {
            position: 'top-right',
            effect: 'stackslide',
            timeout: 5000
          });
          self.props.toggleModal(false, true);
        }).catch(function (error) {
        showAxiosError(error);
      });
    } else {
      Alert.error("Birim seçilmedi.", {
        position: 'top-right',
        effect: 'stackslide',
        timeout: 3000
      });
    }
  }

  readSelectedDeptIdList = () => {
    let idList = [];
    Object.keys(this.state.selectedData).map(id => {
      if (!!this.state.selectedData[id]) {
        idList.push(id);
      }
    })
    return idList;
  };

  render() {
    return <DefaultModal disabled={this.state.btnDisabled}
                         isOpen={this.props.modal}
                         size="lg" closeOnEsc={false}
                         id="title"
                         title={this.props.selectedAuthorizationWay === authorizationWays.DEPARTMENT ? "Organizasyonel Yetki Atama" : "Masraf Yeri Yetki Atama"}
                         confirmBtnText="Kaydet"
                         cancelBtnText="Kapat"
                         onConfirm={this.saveClicked}
                         onCancel={() => this.props.toggleModal(false, false)}>
      <div style={{marginTop: '20px'}}>
        <Form>
          <div className="FormRenk" style={{marginBottom: '10px', paddingLeft: '20px', paddingRight: '20px'}}>
            <label style={{fontWeight: 'bold', display: 'block'}}>
              Yekti Verilen Çalışan
            </label>
            <Col sm={8}>
              {this.renderInput("Ad", "name")}
              {this.renderInput("Soyad", "surname")}
              {this.renderInput("TCKN", "tckn")}
            </Col>

          </div>
          <div className="FormRenk" style={{paddingLeft: '20px', paddingRight: '20px', overflowY:'scroll' , flex: '1', maxHeight: '450px'}}>
            <label style={{fontWeight: 'bold', display: 'block'}}>
              Seçim Ekranı:
            </label>
            {this.state.treeData && this.state.treeData.length > 0 &&
            <AssigningAuthorityTree treeData={this.state.treeData}
                                    selectedData={this.state.selectedData}
                                    onSelect={e => this.setState({selectedData: e, manual: true})}
            />}
          </div>

          {this.state.alert}
        </Form>
      </div>

    </DefaultModal>
  }


}

export default AssigningAuthority;