import React, {Component} from 'react';
import DefaultModal from "../components/DefaultModal";
import Table from "../components/Table/Table";
import {requestK} from "./APIUtils";
import {showAxiosError} from "./helper";

class DisplaySelectionList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      btnDisabled: false,
      alert: null,
      loading: false,
      manual: false,
      content: [],
      total: 0,
      page: 0,
      size: 10,
    };
    this.fetchSelectionListData = this.fetchSelectionListData.bind(this);
    this.renderSelectionList = this.renderSelectionList.bind(this);

  }

  fetchSelectionListData(accessor, direction, page, size) {
    let self = this;
    let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '';
    let params = {
      url: '/employee/readEmployeeIdList?page=' + page + '&size=' + size + sortStr,
      method: "post",
      data: self.props.selection
    };
    self.setState({loading: true, manual: self.manual === true ? false : self.manual});
    requestK(params)
      .then(function (response) {
        self.setState({
          page: page,
          size: size,
          total: response.data.totalElements,
          content: response.data.content,
          loading: false
        });
      }).catch(function (error) {
      self.setState({loading: false});
      showAxiosError(error);
    });
  }

  renderSelectionList() {
    let columns = [
      {
        Header: "Adı",
        accessor: "name"
      },
      {
        Header: "Soyadı",
        accessor: "surname"
      },
      {
        Header: "TCKN",
        accessor: "tckn"

      }
    ];

    return (
      <div className="row col-sm-12" style={{display: "inline-block"}}>

        <div style={{padding: '10px'}}>
          <Table columns={columns}
                 content={this.state.content}
                 loading={this.state.loading}
                 total={this.state.total}
                 page={this.state.page}
                 manual={this.state.manual}
                 size={this.state.size}
                 options={["5", "10", "20", "50", "100"]}
                 fetchData={this.fetchSelectionListData}/>

        </div>

      </div>
    );
  }

  render() {
    return <DefaultModal disable={this.state.btnDisabled}
                         isOpen={this.props.modal}
                         size="lg" closeOnEsc={false}
                         title="Seçilen Çalışanlar"
                         confirmBtnText="Tamam"
                         cancelBtnText="Tüm Şeçimleri Kaldır"
                         onConfirm={() => {
                           this.props.closeModal()
                         }}
                        onCancel={() => {
                           this.props.removeSelected()
                        }}>

      <div style={{marginBottom: '10px', paddingLeft: '20px', paddingRight: '20px'}}>
        {this.renderSelectionList()}
      </div>

    </DefaultModal>


  }


}

export default DisplaySelectionList;
