import React, {Component} from 'react';
import DefaultModal from "../../../../components/DefaultModal";
import {Form, FormGroup} from "reactstrap";
import EmployeeCustomFieldsPage from "../../employeecustom/EmployeeCustomFieldsPage";


class EmployeeSettings extends Component {

  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {

    return <DefaultModal disabled={this.state.btnDisabled}
                         isOpen={this.props.modal}
                         size="lg"
                         title="Çalışan Listesi Ayarları"
                         confirmBtnText="Kapat"
                         cancelBtnStyle={{
                           background: 'linear-gradient(rgba(125, 125, 125, 0.57), rgb(111, 111, 111))',
                           float: 'right', padding: '9px 45px 9px 20px'
                         }}
                         onConfirm={() => this.props.toggleModal(false, false)}>
      <div style={{marginTop: '20px'}}>
        <Form>
          <FormGroup row style={{marginBottom: '10px', paddingLeft: '35px', paddingRight: '5px'}}>
            <EmployeeCustomFieldsPage onModal={true} reload={(e) => this.props.reload(e)} hasGrant={this.props.hasGrant}/>
          </FormGroup>
        </Form>
        {this.state.alert}
      </div>
    </DefaultModal>
  }
}

export default EmployeeSettings;
