import React, {Component} from 'react';
import {showAxiosError} from "../../../../helper/helper";
import {requestK} from "../../../../helper/APIUtils";
import Alert from "react-s-alert";
import {getEmployees} from "../../common/PeykHelper";
import {Col, FormGroup, Label} from "reactstrap";
import Select from "../../../../local_node_modules/react-select"
import TextOverFlowTooltip from "../../../../components/TextOverFlowTooltip";
import Table from "../../../../components/Table/Table";
import SweetAlert from "react-bootstrap-sweetalert";


class SetManagerModal extends Component {

    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            manual: false,
            content: [],
            total: 0,
            page: 0,
            size: 10,
            alert: null
        };
        this.fetchData = this.fetchData.bind(this);
        this.saveClicked = this.saveClicked.bind(this);
        this.renderEmployeeListInfo = this.renderEmployeeListInfo.bind(this);
    }

    componentWillMount() {
        let selection = [];
        if (this.props.selection && this.props.selection.length > 0) {
            selection = this.props.selection;
        }

        this.setState({selection});
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.saveClicked !== this.props.saveClicked) {
            if (this.props.saveClicked && !this.state.loading) {
                this.saveClicked();
            }
        }
    }

    fetchData(accessor, direction, page, size) {
        let self = this;
        let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '';
        let params = {
            url: '/employee/readEmployeeIdList?page=' + page + '&size=' + size + sortStr,
            method: "post",
            data: self.state.selection,
            timeout: 300000
        };
        self.setState({loading: true, manual: self.manual === true ? false : self.manual});
        requestK(params)
            .then(function (response) {
                self.setState({
                    page: page,
                    size: size,
                    total: response.data.totalElements,
                    content: response.data.content,
                    loading: false
                });
            }).catch(function (error) {
            self.setState({loading: false});
            showAxiosError(error);
        });
    }

    saveClicked() {
        let self = this;
        if (!self.state.parentUserId) {
            self.setState({
                alert: (
                    <SweetAlert warning
                                confirmBtnText="Tamam"
                                confirmBtnBsStyle="warning"
                                title="Seçim Yapılmadı"
                                onConfirm={() => this.setState({alert: null}, self.props.saveCompleted())}
                    >
                        Lütfen yönetici seçimi yapınız.
                    </SweetAlert>
                )
            });
        } else if (self.state.selection.length <= 0) {
            self.setState({
                alert: (
                    <SweetAlert warning
                                confirmBtnText="Tamam"
                                confirmBtnBsStyle="warning"
                                title="Çalışan seçimi yapılmadı"
                                onConfirm={() => this.setState({alert: null}, self.props.saveCompleted())}
                    >
                        Lütfen çalışan seçimi yapınız.
                    </SweetAlert>
                )
            });
        } else {
            let params = {
                url: `/employee/saveManager`,
                method: "post",
                data: {
                    recordIds: self.state.selection,
                    parentUserId: self.state.parentUserId
                }
            };
            self.setState({loading: true});
            if (self.state.selection.length > 30) {
                requestK(params)
                    .then(function () {
                        self.setState({
                            loading: false,
                            alert: (
                                <SweetAlert
                                    warning
                                    confirmBtnText="Tamam"
                                    confirmBtnBsStyle="warning"
                                    title="Hatırlatma"
                                    onConfirm={() => {
                                        self.setState({ alert: null });
                                        self.props.closeModal();
                                    }}
                                >
                                    Yönetici bilgisi güncelleme işlemi devam ediyor. İşlem takibinizi bekleyen indirmeler alanından kontrol edebilirsiniz.
                                </SweetAlert>
                            ),
                        });
                        self.props.saveCompleted();
                    })
                    .catch(function (error) {
                        showAxiosError(error);
                        self.props.saveCompleted();
                        self.setState({ loading: false });
                    });
            } else {
                requestK(params)
                    .then(function (response) {
                        let retryCount = 0;
                        const checkDownloadRequestStatus = () => {
                            let downloadRequestParams = {
                                url: `/download-request/checkDownloadRequestStatus`,
                                method: "post",
                                data: response.data
                            };
                            requestK(downloadRequestParams)
                                .then((response) => {
                                    retryCount++;
                                    if (retryCount === 6) {
                                        self.setState({
                                            loading: false,
                                            alert: (
                                                <SweetAlert
                                                    warning
                                                    confirmBtnText="Tamam"
                                                    confirmBtnBsStyle="warning"
                                                    title="Hatırlatma"
                                                    onConfirm={() => {
                                                        self.setState({ alert: null });
                                                        self.props.closeModal();
                                                    }}
                                                >
                                                    Yönetici bilgisi güncelleme işlemi devam ediyor. İşlem takibinizi bekleyen indirmeler alanından kontrol edebilirsiniz.
                                                </SweetAlert>
                                            ),
                                        });
                                        self.props.saveCompleted();
                                        clearInterval(intervalId);
                                    } else {
                                        if (response.data) {
                                            clearInterval(intervalId);
                                            self.props.saveCompleted();
                                            Alert.success("Yönetici bilgisi kaydedildi.", {
                                                position: 'top-right',
                                                effect: 'stackslide',
                                                timeout: 5000
                                            });
                                            self.setState({ loading: false });
                                            self.props.saveCompleted();
                                            self.props.closeModal();
                                        }
                                    }
                                })
                                .catch((error) => {
                                    clearInterval(intervalId);
                                    showAxiosError(error);
                                    self.props.saveCompleted();
                                    self.setState({ loading: false });
                                });
                        };

                        const intervalId = setInterval(checkDownloadRequestStatus, 5000);
                    })
                    .catch(function (error) {
                        showAxiosError(error);
                        self.props.saveCompleted();
                        self.setState({ loading: false });
                    });
            }
        }
    }

    render() {

        return <div style={{marginBottom: '10px', paddingLeft: '20px', paddingRight: '20px'}}>
            <FormGroup row>
                <Label sm={4}>Yönetici Seçimi :</Label>
                <Col sm={8}>
                    <Select.Async multi={false}
                                  ignoreAccents={false}
                                  ignoreCase={false}
                                  value={this.state.manager}
                                  onChange={(e) => this.setState({
                                      manager: e,
                                      parentUserId: e.value
                                  })}
                                  isLoading={true}
                                  autoload={false}
                                  cache={false}
                                  clearable={false}
                                  isDisabled={this.state.loading}
                                  filterOptions={(options, filter, currentValues) => options}
                                  noResultsText="Kayıt bulunamadı."
                                  searchPromptText="Arama için en az 2 karakter giriniz."
                                  placeholder="İsim veya soyisim ile ara..."
                                  loadingPlaceholder="Yükleniyor..."
                                  loadOptions={getEmployees}/>
                </Col>
            </FormGroup>
            {this.renderEmployeeListInfo()}
            {this.state.alert}
        </div>
    }

    removeEmployeeList = (row) => {
        let newList = [];
        this.state.selection && this.state.selection.map(employeeId => {
            if (employeeId !== row.id) {
                newList.push(employeeId);
            }
        });
        this.setState({
            selection: newList,
            manual: true
        }, () => this.fetchData(undefined, undefined, this.state.page, this.state.size));
    };

    clearAllEmployees = () => {
        let conditionalList = this.state.conditionalList;
        this.state.formEmployeeList && this.state.formEmployeeList.map(employeeId => {
            conditionalList.splice(conditionalList.indexOf(employeeId), 1);
        });
        this.setState({formEmployeeList: []});
    };

    renderEmployeeListInfo() {
        let columns = [

            {
                Header: "Adı",
                accessor: "name",
                sortable: false
            },
            {
                Header: "Soyadı",
                accessor: "surname",
                sortable: false
            },
            {
                Header: "Departman",
                accessor: "department",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        {row.department && <TextOverFlowTooltip text={row.department.name} maxLength={20}/>}
                    </div>
                )
            },
            {
                Header: "Görev",
                accessor: "jobTitle",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        <TextOverFlowTooltip text={row.jobTitle} maxLength={20}/>
                    </div>
                )
            },
            {
                Header: "Yönetici",
                accessor: "managerName",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        <TextOverFlowTooltip text={row.managerName} maxLength={20}/>
                    </div>
                )
            },
            {
                Header: "Durum",
                accessor: "statusType",
                sortable: false,
                Cell: row => (
                    <div>
                        {row.statusType === "ACTIVE" &&
                        <span className="text-success">{"Aktif"}</span>}
                        {row.statusType === "PASSIVE" &&
                        <span className="text-warning">{"Pasif"}</span>}
                        {row.statusType === "TERMINATED" &&
                        <span className="text-danger">{"Fesih"}</span>}
                        {row.statusType === "CANDIDATE" &&
                        <span className="text-muted">{"Aday"}</span>}
                    </div>
                )
            },
            {
                Header: "TCKN",
                accessor: "tckn",
                sortable: false
            },
            {
                Header: "İşlemler",
                accessor: "",
                sortable: false,
                Cell: row => (
                    <div style={{textAlign: 'left'}}>
                        <button title="Çalışanı Kaldır" className="btn-icon btn btn-danger btn-sm"
                                disabled={this.state.disabledBtn}
                                style={{margin: '0px 3px'}}
                                onClick={() => this.removeEmployeeList(row)}>
                            <i className="now-ui-icons ui-1_simple-remove"/>
                        </button>
                    </div>
                ),
                width: '35px'
            }
        ];

        return (
            <div className="row col-sm-12" style={{display: 'inline-block'}}>
                <div className="col-sm-12">
                    <label style={{fontWeight: 'bold', display: 'block', textAlign: 'center', margin: '20px 20px 0 0'}}>
                        Seçilen Çalışanlar
                    </label>
                </div>
                <div style={{padding: '10px'}}>
                    <Table columns={columns}
                           content={this.state.content}
                           loading={this.state.loading}
                           total={this.state.total}
                           page={this.state.page}
                           manual={this.state.manual}
                           size={this.state.size}
                           options={["5", "10", "20", "50", "100"]}
                           fetchData={this.fetchData}/>
                </div>
            </div>
        );
    }
}

export default SetManagerModal;
