    downloadMediaForvaccinationcard = () => {
        let self = this;
        let data = self.arrangeDataSetForVC();
        let params = {
            url: `/employee/downloadReportForVC`,
            responseType: 'arraybuffer',
            method: "post",
            data: data
        };
        requestK(params)
            .then(() => {
                Alert.success("İndirme talebiniz işleme alındı. 'Bekleyen İndirmeler'den' takip edebilirsiniz.", {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 8000
                });
            }).catch(function (error) {
            showAxiosError(error);
        });
    }

    openDetailDialog(row) {
        let clone = Object.assign({}, row);
        delete clone["_$visited"];
        this.setState({
            selected: clone.id,
            valid: {},
            displayDetailDialog: true
        });
    }

    closeDetailDialog() {
        this.setState({
            selected: undefined,
            valid: {},
            displayDetailDialog: false
        });
    }

    toggleCheckboxChange(e, row, id) {
        let self = this;
        let checked = e.target.checked;
        let items = self.state.content;
        let selection = self.state.selection;
        if (row === "all") {
            let selection = [];
            if (checked) {
                items.forEach(item => {
                    selection.push(item[id]);
                });
            }
            self.setState({selection: selection, selectedAll: checked});
        } else {
            let index = selection.indexOf(row[id]);
            if (index >= 0) {
                selection = [
                    ...selection.slice(0, index),
                    ...selection.slice(index + 1)
                ];
            } else {
                selection.push(row[id]);
            }
            self.setState({
                selection: selection,
                selectedAll: ((selection.length > 0) ? (selection.length === self.state.content.length) : false)
            });
        }
    }

    isSelected(id) {
        return (this.state.selection.indexOf(id) !== -1);
    }

    getTrStyles(item) {
        return (item.statusType && item.statusType !== 'ACTIVE' && item.statusType !== 'CANDIDATE') ? {
            backgroundColor: 'lightgray',
            webkitTextFillColor: 'darkgray'
        } : {};
    }

    openDisplaySelectionModal = () => {
        this.setState({displaySelectionList: true});
    };

