    activateJoyride() {
        if (this.state.joyrideActive === false) {
            this.setState({flag: 0});
            this.setState({joyrideActive: true});
            if (this.state.width < 1000) {
                this.setState({flag: 1});
            }
        }
    }

    callback(data) {
        if (data.action === 'close' && data.type === 'step:after') {
            this.setState({shouldRun: false});
            this.setState({flag: 0});
        }
        if (data.action === 'stop' || data.action === 'reset' || data.action === 'skip') {
            this.setState({shouldRun: true});
            this.setState({joyrideActive: false});
            this.setState({flag: 0});
            this.activateJoyride();
        }
    }

    checkDownloadState = async () => {
        try {
            const params = {
                url: '/download-request/downloadStateList',
                method: 'get'
            };
            const response = await requestK(params);
            return response.data;
        } catch (error) {
            showAxiosError(error);
            return false;
        }
    };


