import React, {Component} from 'react';
import {Button, Container, Input, Row} from 'reactstrap';
import ReactTable from 'react-table'
import * as XLSX from 'xlsx';
import UploadEmployeeFile from "./UploadEmployeeFile";
import Alert from "react-s-alert";
import moment from "moment";
import {validateField} from "../../../../common/validation";
import SweetAlert from "react-bootstrap-sweetalert";
import {requestK} from "../../../../helper/APIUtils";
import {showAxiosError} from "../../../../helper/helper";

class XlsxImporter extends Component {
    constructor(props) {
        super(props);

        this.state = {
            loading: false,
            headerRowSelected: false,
        };

        this.okClicked = this.okClicked.bind(this);
        this.autoPairing = this.autoPairing.bind(this);
        this.onFileSelect = this.onFileSelect.bind(this);
        this.cancelClicked = this.cancelClicked.bind(this);
        this.checkFieldsOk = this.checkFieldsOk.bind(this);
        this.downloadTemplate = this.downloadTemplate.bind(this);
        this.bulkFileSelected = this.bulkFileSelected.bind(this);
        this.headerDropDownChanged = this.headerDropDownChanged.bind(this);
        XlsxImporter.getCellValue = XlsxImporter.getCellValue.bind(this);
        XlsxImporter.checkDateFormat = XlsxImporter.checkDateFormat.bind(this);
    }

    onFileSelect(file) {
        this.props.onFileNameUpdate(file.name);
        let fileReader = new FileReader();
        fileReader.readAsArrayBuffer(file);
        fileReader.onload = () => {
            this.bulkFileSelected(fileReader.result);
        };
    }

    headerDropDownChanged(key, val) {
        this.setState((prevState, props) => {
            prevState.headerNames[key] = val;
            Object.keys(prevState.headerNames).forEach((key2, index) => {
                if (key2 === key)
                    return;
                if (prevState.headerNames[key2] === val && val !== "outOfScope") {
                    prevState.headerNames[key2] = "";
                }
            });
            return {headerNames: prevState.headerNames};
        });
    }

    bulkFileSelected(fileContent) {
        let data = [];
        let workbook = XLSX.read(fileContent, {type: "array"});
        if (workbook.Directory && workbook.Directory.vba && workbook.Directory.vba.length) {
            Alert.error("Excel macro içermemelidir.", {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 5000
            });
            return;
        }
        let sheet = workbook.Sheets[workbook.SheetNames[0]];
        try {
            let range = XLSX.utils.decode_range(sheet["!ref"]);

            for (let row = range.s.r; row <= range.e.r; row++) {

                let rowData = {};

                for (let col = range.s.c; col <= range.e.c; col++) {
                    let cell = sheet[XLSX.utils.encode_cell({r: row, c: col})];
                    let content = " ";
                    if (cell) {
                        if (cell.t === "s") {
                            content = cell.v;
                        } else if (cell.t === "n") {
                            content = cell.w.toString();
                        } else {
                            content = XlsxImporter.getCellValue(cell)
                        }
                    }
                    if (content && content.length > 0) {
                        let code = "A".charCodeAt(0) + col;
                        rowData[String.fromCharCode(code > 90 ? code + 6 : code)] = content;
                    }
                }
                data.push(rowData);
            }

            let headerNames = {};
            if (data.length > 0) {
                Object.keys(data[0]).forEach((key, index) => {
                    headerNames[key] = "";
                });
            }

            this.setState({bulkCreateData: data, headerNames: headerNames});
            this.autoPairing(data);
        } catch (error) {
            Alert.warning('Yüklenen excel dokümanı hasarlıdır. Yeniden hasarsız bir excel dokümanı yükleyiniz.', {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 5000
            });
        }
    }

    static getCellValue(cell) {
        if (cell.v.toString() === cell.w.toString()) {
            return cell.w;
        } else {
            let date = XLSX.SSF.parse_date_code(cell.v);
            if (date)
                return moment(new Date(parseInt(date.y), parseInt(date.m) - 1, parseInt(date.d))).format("YYYY-MM-DD");
            else
                return cell.v.toString();
        }
    }

    autoPairing(data) {
        let columnNameList = [
            {text: "tckn", code: "tckn"},
            {text: "adı", code: "name"},
            {text: "soyadı", code: "surname"},
            {text: "departman kodu", code: "departmentName"},
            {text: "departmanı", code: "departmentCode"},
            {text: "görevi", code: "jobTitle"},
            {text: 'durum bilgisi', code: 'status'},
            {text: "ünvan", code: "jobTitle"},
            {text: "unvan", code: "jobTitle"},
            {text: "yönetici tckn", code: "managerTckn"},
            {text: "kep", code: "kepAddress"},
            {text: "mail", code: "email"},
            {text: "e-posta adresi", code: "email"},
            {text: "telefon numarası", code: "telephone"},
            {text: 'entegrasyon kodu', code: 'integrationCode'},
            {text: 'İş Sözleşmesi Türü', code: 'employmentContract'},
            {text: 'ücret', code: 'salary'},
            {text: 'VIP', code: 'vip'},
            {text: 'masraf yeri', code: 'expenditureDepName'},
            {text: 'çalışma şekli', code: 'employmentStatus'},
            {text: 'cinsiyet', code: 'gender'},
            {text: 'adres', code: 'address'},
            {text: 'doğum yeri', code: 'cityOfBirth'},
            {text: 'eğitim durumu', code: 'educationLevel'},
            {text: 'askerlik durumu', code: 'militaryStatus'},
            {text: 'Grup', code: 'groups'},
            {text: 'Yıllık İzin Hakkı', code: 'remainingAnnualVacation'},
            {text: 'Mazeret İzni Hakkı', code: 'remainingExcuseVacation'},
            {text: 'Doğum Tarihi', code: 'dateOfBirth'},
            {text: 'İşe Başlama Tarihi', code: 'employmentStartDate'},
            {text: "İkinci Yönetici Tckn", code: "secondManagerTckn"},
            {text: 'Bağlı Olduğu Yönetici', code: 'managerName'},
            {text: 'Bağlı Olduğu İkinci Yönetici', code: 'secondManagerName'},
            {text: "Üçüncü Yönetici Tckn", code: "thirdManagerTckn"},
            {text: 'Bağlı Olduğu Üçüncü Yönetici', code: 'thirdManagerName'},

            // {text: "gorev", code: "jobTitle"},
            // {text: "unvan", code: "jobTitle"},
            // {text: "yoneti", code: "managerTckn"},
            // {text: "t.c", code: "tckn"},
            // {text: 'basla', code: 'employmentStartDate'},
            // {text: 'calisma', code: 'employmentStatus'},
            // {text: 'yillik', code: 'remainingAnnualVacation'},
            // {text: 'dogum tarih', code: 'dateOfBirth'},
            // {text: 'dogum yer', code: 'cityOfBirth'},
            // {text: 'egitim', code: 'educationLevel'},
            // {text: 'sozlesmesi', code: 'employmentContract'},
            // {text: 'Masraf Yeri', code: 'expenditureDepName'}
        ];

        if (this.props.titleList.length > 0)
            this.props.titleList.map((item, index) => {
                let titleCode = "title" + index;
                columnNameList.push({text: item.title, code: titleCode});
            });
        let firstRow = data[0];
        let compareResult = true;

        Object.keys(firstRow).forEach((key, index) => {
            let check = false;
            columnNameList.forEach(col => {
                let regexp = "(?:^|\W)" + col.text.toLocaleLowerCase() + "(?:$|\W)";
                let searchRegex = new RegExp(regexp,'gi');
                let str = firstRow[key].toLocaleLowerCase();
                if (str.search(searchRegex) > -1) {
                    check = true;
                    this.headerDropDownChanged(key, col.code);
                }
            });
            compareResult = compareResult && check;
        });

        if (compareResult) {
            this.setState({headerRowSelected: true});
        }
    }

    okClicked() {

        if (this.checkFieldsOk()) {

            let bulkCreateDataFiltered;
            if (this.state.bulkCreateData && this.state.headerRowSelected) {
                bulkCreateDataFiltered = this.state.bulkCreateData.slice(1);
            } else {
                bulkCreateDataFiltered = this.state.bulkCreateData;
            }
            let hasError = false;
            let rowNumber = 2;
            let errorTextList = "";
            let realData = bulkCreateDataFiltered.map((val) => {
                let obj = {};
                let customFields = {};
                Object.keys(this.state.headerNames).forEach((key, i) => {
                    let header = this.state.headerNames[key];
                    if (header) {
                        let value = val[key];
                        value = value && value.trim();
                        if (! header.includes("title") && (new RegExp(/^(\s)*(\r)*(\t)*(\n)*[=+\-@,]+/g).test(value) || new RegExp(/[<>]+/g).test(value))) {
                            let error = "Satır: " + rowNumber + " [ " + value + " ] verisinde izin verilmeyen karakter ( =, +, -, @, <, > ) kullanımı vardır.";
                            errorTextList = errorTextList + (!hasError ? error : ("\r\n" + error));
                            hasError = true;
                        }
                        if ((header === "remainingAnnualVacation" || header === "remainingExcuseVacation") && value && value.length > 0) {
                            if (value.toString().includes(",")) {
                                value = value.toString().replace(",", ".");
                            }
                        } else if ((header === "salary") && value) {
                            if (value.toString().includes(",")) {
                                value = value.toString().split(",").join(".");
                            }
                            let firstIndex = value.toString().indexOf(".");
                            let lastIndex = value.toString().lastIndexOf(".");

                            if (firstIndex !== -1 || lastIndex !== -1) {
                                while (firstIndex !== lastIndex) {
                                    value = value.toString().replace(".", "");
                                    firstIndex = value.toString().indexOf(".");
                                    lastIndex = value.toString().lastIndexOf(".");
                                }
                            }
                        } else if (header === "departmentName" && value && value.length > 0) {
                            if (value.toString().includes("\r\n")) {
                                value = value.toString().split("\r\n").join("");
                            }
                            if (value.toString().includes(" ")) {
                                value = value.toString().split(" ").join("");
                            }
                        } else if ((header === "tckn" || header === "telephone") && value && value.length > 0) {
                            value = parseInt(value).toString();
                        } else if (header === "vip") {
                            value = (value != null && (value.toLowerCase() === 'evet' || value.toLowerCase() === 'true'));
                        }
                        let error = this.checkFormatValidity(rowNumber, header, value);
                        if (error && error.length) {
                            errorTextList = errorTextList + (!hasError ? error : ("\r\n" + error));
                            hasError = true;
                        }
                        if (header.toString().includes("title")) {
                            let originalHeaderText = header;
                            this.props.headerNames.map(headerObj => {
                                if (headerObj.code === header) {
                                    originalHeaderText = headerObj.name;
                                }
                            });
                            customFields[originalHeaderText] = value;
                        } else {
                            obj[header] = value;
                        }
                    }
                    obj["customFields"] = JSON.stringify(customFields);
                });
                rowNumber++;
                return obj;
            });

            if (this.props.onSubmit && realData && !hasError) {
                this.props.onSubmit(realData);
            } else if (hasError) {
                this.setState({
                    alert: <SweetAlert error
                                       confirmBtnText="Tamam"
                                       confirmBtnBsStyle="error"
                                       title="Hata"
                                       onConfirm={() => this.setState({alert: null})}>
                        <div>
                            <label>Excel'de formatı hatalı satırlar bulunmaktadır. Hata içeriği aşağıdaki
                                gibidir.</label>
                            <Input type="textarea" rows={5}
                                   style={{height: '200px', border: '1px solid #E3E3E3', marginTop: '2px'}}
                                   value={errorTextList}
                                   disable={true}/>
                        </div>
                    </SweetAlert>
                });
            }
        } else {
            Alert.error("Seçim yapılmamış başlık var.", {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 5000
            });
        }
    }

    checkFormatValidity = (rowNumber, header, value) => {
        switch (header) {
            case "tckn":
                if (value && !value.match(/^[0-9]+$/)) {
                    return "Satır: " + rowNumber + " - TCKN sadece rakam içermelidir."
                } else if (value && value.length !== 11) {
                    return "Satır: " + rowNumber + " - TCKN 11 haneli olmalıdır."
                }
                break;
            case "managerTckn":
                if (value && !value.match(/^[0-9]+$/)) {
                    return "Satır: " + rowNumber + " - Yönetici TCKN sadece rakam içermelidir."
                } else if (value && value.length !== 11) {
                    return "Satır: " + rowNumber + " - Yönetici TCKN 11 haneli olmalıdır."
                }
                break;
            case "telephone":
                if (value && !value.match(/^[0-9]+$/)) {
                    return "Satır: " + rowNumber + " - Telefon bilgisi sadece rakam içermelidir."
                } else if (value && value.length !== 11 && value.length !== 10) {
                    return "Satır: " + rowNumber + " - Telefon bilgisi 10-11 haneli olabilmektedir."
                }
                break;
            case "remainingAnnualVacation":
            case "remainingExcuseVacation":
                if (value && !value.match(/^[0-9.]+$/)) {
                    return "Satır: " + rowNumber + " - Kalan izin bilgileri sayı olarak girilmelidir."
                } else if (value && value.trim() && value.trim().length && value > 99.99) {
                    return "Satır: " + rowNumber + " - Kalan izin bilgisi 99.99'dan büyük olamaz"
                }
                break;
            case "salary":
                if (value && !value.match(/^[0-9.]+$/)) {
                    return "Satır: " + rowNumber + " - Ücret bilgisinde formata uygun olmayan karakter mevcuttur."
                }
                break;
            case "departmentName":
                if (value && !value.match(/^[a-zA-Z0-9üÜığĞİşŞçÇöÖ./&\s\-]+$/)) {
                    return "Satır: " + rowNumber + " - Departman Kodu sadece rakam, harf, nokta veya tire işareti içermeli ve bitişik ifadelerden oluşmalıdır."
                }
                break;
            case "email":
            case "kepAddress":
                if (value && !validateField("email", "3,255", value).valid) {
                    return "Satır: " + rowNumber + " - Email formatına uygun olmayan veri vardır."
                }
                break;
            case "employmentStartDate":
            case "dateOfBirth":
                value = XlsxImporter.checkDateFormat(header, value);
                if (value === -1) {
                    if (header === "dateOfBirth")
                        return "Satır: " + rowNumber + " - Doğum Tarihi kolonu - Tarih formatı uygun değildir. GÜN-AY-YIL formatını tercih ediniz. Örneğin: 19-08-2019"
                    else
                        return "Satır: " + rowNumber + " - İşe Başlama Tarihi kolonu - Tarih formatı uygun değildir. GÜN-AY-YIL formatını tercih ediniz. Örneğin: 19-08-2019"
                }
                break;
        }
    };

    static checkDateFormat(key, value) {
        if (value && value !== "" && (key === 'employmentStartDate' || key === 'dateOfBirth')) {
            value = value.toString()
                .split(" ").join("-")
                .replace(/\//g, "-")
                .replace(/,/g, "-")
                .replace(/\./g, "-");

            if (moment(value, "DD-MM-YYYY", true).isValid()) {
                return moment(value, "DD-MM-YYYY").format("YYYY-MM-DD");
            } else if (moment(value, "DD-MM-YY", true).isValid()) {
                return moment(value, "DD-MM-YY").format("YYYY-MM-DD");
            } else if (moment(value, "YYYY-MM-DD", true).isValid()) {
                return value;
            } else if (moment(value, "MM-DD-YYYY", true).isValid()) {
                return moment(value, "MM-DD-YYYY").format("YYYY-MM-DD");
            } else if (moment(value, "MM-DD-YY", true).isValid()) {
                return moment(value, "MM-DD-YY").format("YYYY-MM-DD");
            } else if (moment(value, "YYYY-DD-MM", true).isValid()) {
                return moment(value, "YYYY-DD-MM").format("YYYY-MM-DD");
            } else {
                return -1;
            }
        } else {
            return value;
        }
    }

    checkFieldsOk() {
        let allFieldSelected = true;
        if (this.state.headerNames && Object.keys(this.state.headerNames).length > 0) {
            Object.keys(this.state.headerNames).forEach(key => {
                if (this.state.headerNames[key] === '')
                    allFieldSelected = false;
            });
        } else {
            allFieldSelected = false;
        }
        return allFieldSelected;
    }

    cancelClicked() {
        if (this.props.onCancel)
            this.props.onCancel();
    }

    downloadTemplate() {
        let self = this;
        self.setState({loading: true});
        let data = [];
        let csv = self.props.headerNames.map((e) => {
            return e.name;
        });
        data.push(csv);
        let csvExample = self.props.exampleNames.map((e) => {
            return e.name;
        });
        data.push(csvExample);

        let ws = XLSX.utils.json_to_sheet(data, {skipHeader: true});
        let wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws, "Çalışan Şablon");
        let wopts = {bookType: 'xlsx', bookSST: false, type: 'base64'};
        let wbout = XLSX.write(wb, wopts);
        let params = {
            url: `/employee/addDepartmentIntegrationCodesToExcel`,
            method: "post",
            responseType: 'arraybuffer',
            data: wbout
        };
        requestK(params)
            .then(function (response) {
                let link = document.createElement('a');
                link.href = window.URL.createObjectURL(new Blob([response.data], {type: "application/vnd.ms-excel"}));
                link.download = "Toplu_Calisan_Sablon.xlsx";
                document.body.appendChild(link);
                link.click();
                self.setState({loading: false});
            }).catch(function (error) {
            self.setState({loading: false});
            showAxiosError(error);
        });
    }

    renderMainTable(data) {

        if (!data || data.length <= 0)
            return "";

        let firstRow = data[0];
        let columns = [];

        Object.keys(firstRow).forEach((key, index) => {
            columns.push({
                Header: <Input type="select"
                               value={this.state.headerNames[key]}
                               onChange={(e) => {
                                   this.headerDropDownChanged(key, e.target.value)
                               }}
                               style={{backgroundColor: !this.state.headerNames[key] ? 'orange' : ''}}>
                    {
                        [
                            <option value="" style={{backgroundColor: '#ffffffc2'}}>Seçiniz</option>,
                            <option value="outOfScope" style={{backgroundColor: '#cccccc'}}>Kapsam Dışı</option>,
                            ...this.props.headerNames.map(
                                e => <option value={e.code} style={{backgroundColor: '#ffffffc2'}}>{e.name}</option>
                            )
                        ]
                    }
                </Input>,
                columns: [
                    {
                        Header: key,
                        accessor: key
                    }
                ]
            });
        });

        if (this.state.headerRowSelected)
            data = data.slice(1);

        return <ReactTable data={data} columns={columns} defaultPageSize={5}
                           previousText={"Önceki"} nextText={"Sonraki"} pageText={"Sayfa"}
                           ofText={"Sayfa Toplamı:"} rowsText={"kayıt"}
                           style={{overflowWrap: 'break-word'}}/>;
    }

    render() {

        return (
            <div>
                {!this.state.bulkCreateData &&
                    <Container>
                        <div className="row" style={{marginBottom: "15px"}}>
                            <Button color="info" title="Uygun formattaki şablon dosyasını indir"
                                    disabled={this.state.loading}
                                    onClick={this.downloadTemplate}>
                                <span>Şablon İndir&nbsp;&nbsp;</span>
                                <i className="fa fa-download"/>
                            </Button>
                            <div id='MessageHolder'/>
                        </div>
                        <UploadEmployeeFile onFileSelect={this.onFileSelect}/>
                    </Container>
                }
                {this.state.bulkCreateData &&
                    <div>
                        {this.renderMainTable(this.state.bulkCreateData)}
                        <Row style={{marginLeft: "2%"}}>
                            <Button color="secondary" onClick={this.cancelClicked}>
                                İptal
                            </Button>
                            <Button color="primary" onClick={this.okClicked}>
                                Kaydet
                            </Button>
                            <div style={{marginLeft: "5%", marginTop: "2.3%"}}>
                                <Input type="checkbox" onChange={e => {
                                    this.setState({headerRowSelected: e.target.checked})
                                }} checked={this.state.headerRowSelected}/>
                                <label>İlk satırda başlıklar var</label>
                            </div>
                        </Row>
                    </div>
                }
                {this.state.alert}
            </div>
        );
    }
}

export default XlsxImporter;