import React, {Component} from 'react';
import {checkGrantToDisplay, showAxiosError} from "../../../../helper/helper";
import DefaultModal from "../../../../components/DefaultModal";
import Alert from "react-s-alert";
import {Button, Col, Form, FormGroup, Input, Label, Modal, ModalBody, ModalFooter, ModalHeader} from "reactstrap";
import Select from "../../../../local_node_modules/react-select"
import SweetAlertExt from "../../../../components/SweetAlertExt";
import SetManagerModal from "./SetManagerModal";
import UpdateStatusModal from "./UpdateStatusModal";
import {EMPLOYEES} from "../../../../common/authorities/GrantDef";
import TwoStepVerificationModal from "./TwoStepVerificationModal";
import UploadEmployeeFile from "./UploadEmployeeFile";
import Row from "react-bootstrap/Row";
import * as XLSX from 'xlsx';
import {isValidTckn} from "../../../../helper/TCKimlikNoValidator";
import SweetAlert from "react-bootstrap-sweetalert";
import {requestK} from "../../../../helper/APIUtils";
import AddEmployeeToGroupModal from "./AddEmployeeToGroupModal";
import DeleteEmployeeFromGroupModal from "./DeleteEmployeeFromGroupModal";


class EmployeeTaskScreens extends Component {

    constructor(props) {
        super(props);
        this.state = {
            btnDisabled: false,
            saveClicked: false,
            alert: null,
            showExcelImport: true,
            selection: []
        };
        this.saveClicked = this.saveClicked.bind(this);
        this.checkDownloadState = this.checkDownloadState.bind(this);

    }

    componentDidMount() {
        if (this.props.selection && this.props.selection.length > 0)
            this.setState({selection: this.props.selection});
    }

    onFileSelect = (file) => {
        let fileReader = new FileReader();
        fileReader.readAsArrayBuffer(file);
        fileReader.onload = () => {
            this.bulkFileSelected(fileReader.result);
        };
    };

    bulkFileSelected = (fileContent) => {
        let data = [];
        let invalidData = [];
        let multiData = [];
        let workbook = XLSX.read(fileContent, {type: "array"});
        if (workbook.Directory && workbook.Directory.vba && workbook.Directory.vba.length) {
            Alert.error("Excel macro içermemelidir.", {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 5000
            });
            return;
        }
        let sheet = workbook.Sheets[workbook.SheetNames[0]];
        let range = XLSX.utils.decode_range(sheet["!ref"]);

        let isFirstRow = true;
        let hasTcknTitle = false;
        for (let row = range.s.r; row <= range.e.r; row++) {
            let validTcknFound = false;
            let multiTcknFound = false;
            let allRowEmpty = true;
            for (let col = range.s.c; col <= range.e.c; col++) {
                let cell = sheet[XLSX.utils.encode_cell({r: row, c: col})];
                let content = "";
                if (cell) {
                    allRowEmpty = false;
                    if (cell.t === "s") {
                        content = cell.v;
                    } else if (cell.t === "n") {
                        content = parseInt(cell.w).toString();
                    }
                }
                if (content && content.length > 0) {
                    if (isValidTckn(content)) {
                        data.push(content);
                        if (!validTcknFound) {
                            validTcknFound = true;
                        } else {
                            multiTcknFound = true;
                        }
                    } else if (isFirstRow && (content.toString().toLowerCase().includes("tc") || content.toString().toLowerCase().includes("t.c"))) {
                        hasTcknTitle = true;
                    }
                }
            }
            if (!(isFirstRow && hasTcknTitle) && !allRowEmpty) {
                if (!validTcknFound) {
                    let invalid = sheet[XLSX.utils.encode_cell({r: row, c: 0})].v;
                    if (range.e.c > 1) {
                        invalid = invalid + "/" + sheet[XLSX.utils.encode_cell({r: row, c: 1})].v;
                    }
                    if (range.e.c > 2) {
                        invalid = invalid + "/" + sheet[XLSX.utils.encode_cell({r: row, c: 2})].v;
                    }
                    if (!invalidData.length) {
                        invalidData.push("Satır: " + (row + 1) + " - " + invalid + "...");
                    } else {
                        invalidData.push("\r\n" + "Satır: " + (row + 1) + " - " + invalid + "...");
                    }
                } else if (multiTcknFound) {
                    let invalid = sheet[XLSX.utils.encode_cell({r: row, c: 0})].v;
                    if (range.e.c > 1) {
                        invalid = invalid + "/" + sheet[XLSX.utils.encode_cell({r: row, c: 1})].v;
                    }
                    if (range.e.c > 2) {
                        invalid = invalid + "/" + sheet[XLSX.utils.encode_cell({r: row, c: 2})].v;
                    }
                    if (!multiData.length) {
                        multiData.push("Satır: " + (row + 1) + " - " + invalid + "...");
                    } else {
                        multiData.push("\r\n" + "Satır: " + (row + 1) + " - " + invalid + "...");
                    }
                }
            }
            isFirstRow = false;
        }

        if (!invalidData.length && !multiData.length) {
            this.saveListFromExcel(data);
        } else {
            this.setState({
                alert: (
                    <SweetAlert warning
                                confirmBtnText="Tamam"
                                confirmBtnBsStyle="warning"
                                title="Yükleme Başarısız"
                                onConfirm={() => this.setState({alert: null})}
                    >
                        <div>
                            {!!invalidData.length &&
                                <label>{"Excel'de her satırda geçerli Tckn beklenmektedir. Buna uyumsuz satır bulunmaktadır. Aşağıda listelenmiştir."}</label>}
                            {!!invalidData.length &&
                                <Input type="textarea" rows={5}
                                       style={{height: '200px', border: '1px solid #E3E3E3', marginTop: '2px'}}
                                       value={invalidData}
                                       disable={true}/>}
                            {!!multiData.length &&
                                <label>{"Excel'de bir satırda bir tckn olmalıdır. Buna uyumsuz satır bulunmaktadır. Aşağıda listelenmiştir."}</label>}
                            {!!multiData.length &&
                                <Input type="textarea" rows={5}
                                       style={{height: '200px', border: '1px solid #E3E3E3', marginTop: '2px'}}
                                       value={multiData}
                                       disable={true}/>}
                        </div>
                    </SweetAlert>
                )
            });
        }
    };

    saveListFromExcel = (list) => {
        let self = this;
        let params = {
            url: `/employee/getEmployeeListByExcel`,
            method: "post",
            data: list
        };
        self.setState({loading: true});
        requestK(params)
            .then(function (response) {
                self.setState({selection: response.data, loading: false, manual: true});
                if (response.data.length) {
                    self.setState({showExcelImport: false});
                    self.setState({
                        alert: (
                            <SweetAlert info
                                        confirmBtnText="Tamam"
                                        confirmBtnBsStyle="success"
                                        title="Excel'den seçim yapıldı."
                                        onConfirm={() => {
                                            self.setState({alert: null});
                                            self.saveClicked();
                                        }}
                            >
                                <div>
                                    <label>{response.data.length + " adet seçim yapıldı."}</label>
                                    {(list.length - response.data.length > 0) && <label>
                                        <i className="fa fa-exclamation-triangle"/>
                                        &nbsp;&nbsp;{"Kayıtlı olmayan TCKN'ler vardır."}
                                    </label>}
                                </div>
                            </SweetAlert>
                        )
                    });
                } else {
                    self.setState({
                        alert: (
                            <SweetAlert warning
                                        confirmBtnText="Tamam"
                                        confirmBtnBsStyle="warning"
                                        title="Excel'den seçim yapılamadı."
                                        onConfirm={() => self.setState({alert: null})}
                            >
                                Çalışan listenizde uyuşan TCKN bulunmamaktadır.
                            </SweetAlert>
                        )
                    });
                }
            })
            .catch(function (error) {
                showAxiosError(error);
                self.setState({loading: false, manual: true});
            });
    };

    saveClicked() {
        if (this.state.selection.length > 0) {
            Alert.success("Çalışan seçimi yapıldı", {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 3000
            });
        } else {
            Alert.error("En az bir çalışan seçmelisiniz.", {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 3000
            });
        }
    }

    checkDownloadState = async () => {
        try {
            const params = {
                url: '/download-request/downloadStateList',
                method: 'get'
            };
            const response = await requestK(params);
            return response.data;
        } catch (error) {
            showAxiosError(error);
            return false;
        }
    };

    render() {
        return (
            <Modal isOpen={this.props.modal} size="lg">
                <ModalHeader toggle={() => this.props.toggleModal(false, true)}>Çoklu İşlem Ekranı</ModalHeader>
                <ModalBody>
                    <div className='col-sm-12'>
                        <div style={{marginTop: '20px'}}>
                            <Form>
                                <div className="FormRenk"
                                     style={{marginBottom: '10px', paddingLeft: '20px', paddingRight: '20px'}}>
                                    <FormGroup row>
                                        <Label sm={4}>İşlem Seçimi :</Label>
                                        <Col sm={8}>
                                            <Select value={this.state.selectedTask}
                                                    clearable={false}
                                                    noResultsText="Tanımlanmış işlem bulunamadı"
                                                    clearValueText="Temizle"
                                                    placeholder="Seçiniz..."
                                                    onChange={e => this.setState({
                                                        selectedTask: e,
                                                        selectedScreen: e.value
                                                    })}
                                                    options={[
                                                        {value: 'setManager', label: 'Yönetici Ata'},
                                                        {value: 'updateStatus', label: 'Durum Güncelle'},
                                                        {value: 'addEmployeeToGroup', label: 'Gruba Çalışan Ekle'},
                                                        {value: 'deleteEmployeeFromGroup', label: 'Grubtan Çalışan Çıkar'},
                                                        /*{value: 'twoStepVerification', label: 'Doğrulama Kodu ile Giriş'},*/
                                                    ]}/>
                                        </Col>
                                    </FormGroup>
                                    {this.state.selectedScreen && this.state.showExcelImport && !(this.props.selection.length > 0) &&
                                        <div className="col-xs-12 col-md-12">
                                            <UploadEmployeeFile onFileSelect={this.onFileSelect}
                                                                title={<div style={{textAlign: 'center'}}>
                                                                    <Row><img
                                                                        src={"data:image/jpeg;base64,"}
                                                                        style={{height: '65px'}}
                                                                        alt={"employee excel upload"}/></Row>
                                                                    <Row><span>Çalışan seçimini Excel'den yükleyerek yapmak için tıklayın</span></Row>
                                                                </div>}/>
                                        </div>}
                                    {this.state.selectedScreen === "setManager" && this.state.selection.length > 0 &&
                                        <SetManagerModal
                                            selection={this.state.selection}
                                            modal={this.state.selectedScreen === "setManager"}
                                            saveClicked={this.state.saveClicked}
                                            saveCompleted={() => this.setState({
                                                btnDisabled: false,
                                                saveClicked: false
                                            })}
                                            closeModal={() => this.props.toggleModal(false, true)}/>
                                    }
                                    {this.state.selectedScreen === "updateStatus" && this.state.selection.length > 0 &&
                                        <UpdateStatusModal
                                            selection={this.state.selection}
                                            modal={this.state.selectedScreen === "updateStatus"}
                                            saveClicked={this.state.saveClicked}
                                            saveCompleted={() => this.setState({
                                                btnDisabled: false,
                                                saveClicked: false
                                            })}
                                            closeModal={() => this.props.toggleModal(false, true)}/>
                                    }
                                    {/*this.state.selectedScreen === "twoStepVerification" && this.state.selection.length > 0 &&
                                        <TwoStepVerificationModal
                                            selection={this.state.selection}
                                            modal={this.state.selectedScreen === "twoStepVerification"}
                                            saveClicked={this.state.saveClicked}
                                            saveCompleted={() => this.setState({
                                                btnDisabled: false,
                                                saveClicked: false
                                            })}
                                            closeModal={() => this.props.toggleModal(false, true)}/>
                                    */}
                                    {this.state.selectedScreen === "addEmployeeToGroup" && this.state.selection.length > 0 &&
                                        <AddEmployeeToGroupModal
                                            selection={this.state.selection}
                                            modal={this.state.selectedScreen === "addEmployeeToGroup"}
                                            saveClicked={this.state.saveClicked}
                                            saveCompleted={() => this.setState({
                                                btnDisabled: false,
                                                saveClicked: false
                                            })}
                                            closeModal={() => this.props.toggleModal(false, true)}/>
                                    }
                                    {this.state.selectedScreen === "deleteEmployeeFromGroup" && this.state.selection.length > 0 &&
                                        <DeleteEmployeeFromGroupModal
                                            selection={this.state.selection}
                                            modal={this.state.selectedScreen === "deleteEmployeeFromGroup"}
                                            saveClicked={this.state.saveClicked}
                                            saveCompleted={() => this.setState({
                                                btnDisabled: false,
                                                saveClicked: false
                                            })}
                                            closeModal={() => this.props.toggleModal(false, true)}/>
                                    }
                                </div>
                            </Form>
                            {this.state.alert}
                        </div>
                    </div>
                </ModalBody>
                <ModalFooter>
                    <Button title="İptal" style={{paddingLeft: '16px', width: '100px'}} color="secondary"
                            onClick={() => this.props.toggleModal(false, true)}>
                        İptal
                    </Button>
                    <Button title="Kaydet" style={{paddingLeft: '16px', width: '100px'}} color="primary"
                            disabled={this.state.btnDisabled || !checkGrantToDisplay(this, [EMPLOYEES])}
                            onClick={async () => {
                                if (!!this.state.selectedTask) {
                                    if (this.state.selection.length > 0) {
                                        const activeDownloadCount = await this.checkDownloadState();
                                        this.setState({
                                            alert: (
                                                <SweetAlertExt showCancel warning
                                                               confirmBtnText="Evet"
                                                               cancelBtnText="Hayır"
                                                               confirmBtnBsStyle="info"
                                                               cancelBtnBsStyle="default"
                                                               title="Emin misiniz?"
                                                               onConfirm={() => this.setState({
                                                                   saveClicked: true,
                                                                   alert: null,
                                                                   btnDisabled: true
                                                               })}
                                                               onCancel={() => this.setState({
                                                                   saveClicked: false,
                                                                   alert: null
                                                               })}
                                                >
                                                    {activeDownloadCount > 0 && (
                                                        <div style={{ fontSize: '15px', marginBottom: '10px' }}>
                                                            Şu anda {activeDownloadCount} adet toplu ekleme/güncelleme veya çoklu işleminiz devam etmektedir.
                                                            Yeni işleminiz sıraya alınacaktır. Durum takibini "Bekleyen İndirmeler" alanından yapabilirsiniz.
                                                        </div>
                                                    )}
                                                    {this.state.selectedScreen === "setManager" && (
                                                        <div style={{ fontSize: '15px', marginBottom: '10px' }}>
                                                   Yönetici atama işlemi "Bağlı Olduğu 1. Yönetici" için yapılacaktır. Eğer mevcut 1. yönetici tanımlıysa, bu yönetici güncellenecektir."
                                                </div>
                                                    )}
                                                    Kaydetme işlemi gerçekleştirilecek.
                                                </SweetAlertExt>
                                            )
                                        });
                                    } else {
                                        this.setState({
                                            alert: (
                                                <SweetAlert warning
                                                            confirmBtnText="Tamam"
                                                            confirmBtnBsStyle="warning"
                                                            title="Seçim Yapılmadı"
                                                            onConfirm={() => this.setState({alert: null})}
                                                >
                                                    Lütfen çalışan seçimi yapınız.
                                                </SweetAlert>
                                            )
                                        });
                                    }
                                } else {
                                    this.setState({
                                        alert: (
                                            <SweetAlert warning
                                                        confirmBtnText="Tamam"
                                                        confirmBtnBsStyle="warning"
                                                        title="Seçim Yapılmadı"
                                                        onConfirm={() => this.setState({alert: null})}
                                            >
                                                Lütfen işlem seçimi yapınız.
                                            </SweetAlert>
                                        )
                                    });
                                }

                            }}>
                        Kaydet
                    </Button>
                </ModalFooter>
            </Modal>
        );
    }

    render2() {
        return <DefaultModal
            disabled={this.state.btnDisabled || !checkGrantToDisplay(this, [EMPLOYEES])}
            isOpen={this.props.modal}
            size="lg"
            title="Çoklu İşlem Ekranı"
            confirmBtnText="Kaydet"
            cancelBtnText="Kapat"
            onConfirm={() => {
                if (!!this.state.selectedTask) {
                    if (this.state.selection.length > 0) {
                        this.setState({
                            alert: (
                                <SweetAlertExt showCancel warning
                                               confirmBtnText="Evet"
                                               cancelBtnText="Hayır"
                                               confirmBtnBsStyle="info"
                                               cancelBtnBsStyle="default"
                                               title="Emin misiniz?"
                                               onConfirm={() => this.setState({
                                                   saveClicked: true,
                                                   alert: null,
                                                   btnDisabled: true
                                               })}
                                               onCancel={() => this.setState({saveClicked: false, alert: null})}
                                >
                                    Kaydetme işlemi gerçekleştirilecek.
                                </SweetAlertExt>
                            )
                        });
                    } else {
                        this.setState({
                            alert: (
                                <SweetAlert warning
                                            confirmBtnText="Tamam"
                                            confirmBtnBsStyle="warning"
                                            title="Seçim Yapılmadı"
                                            onConfirm={() => this.setState({alert: null})}
                                >
                                    Lütfen çalışan seçimi yapınız.
                                </SweetAlert>
                            )
                        });
                    }
                } else {
                    this.setState({
                        alert: (
                            <SweetAlert warning
                                        confirmBtnText="Tamam"
                                        confirmBtnBsStyle="warning"
                                        title="Seçim Yapılmadı"
                                        onConfirm={() => this.setState({alert: null})}
                            >
                                Lütfen işlem seçimi yapınız.
                            </SweetAlert>
                        )
                    });
                }

            }}
            onCancel={() => this.props.toggleModal(false, true)}>
            <div style={{marginTop: '20px'}}>
                <Form>
                    <div className="FormRenk" style={{marginBottom: '10px', paddingLeft: '20px', paddingRight: '20px'}}>
                        <FormGroup row>
                            <Label sm={4}>İşlem Seçimi :</Label>
                            <Col sm={8}>
                                <Select value={this.state.selectedTask}
                                        clearable={false}
                                        noResultsText="Tanımlanmış işlem bulunamadı"
                                        clearValueText="Temizle"
                                        placeholder="Seçiniz..."
                                        onChange={e => this.setState({selectedTask: e, selectedScreen: e.value})}
                                        options={[
                                            {value: 'setManager', label: 'Yönetici Ata'},
                                            {value: 'updateStatus', label: 'Durum Güncelle'},
                                            {value: 'addEmployeeToGroup', label: 'Gruba Çalışan Ekle'},
                                            {value: 'deleteEmployeeFromGroup', label: 'Grubtan Çalışan Çıkar'},
                                            {value: 'twoStepVerification', label: 'Doğrulama Kodu ile Giriş'},
                                        ]}/>
                            </Col>
                        </FormGroup>
                        {this.state.selectedScreen && this.state.showExcelImport && !(this.props.selection.length > 0) &&
                            <div className="col-xs-12 col-md-12">
                                <UploadEmployeeFile onFileSelect={this.onFileSelect}
                                                    title={<div style={{textAlign: 'center'}}>
                                                        <Row><img
                                                            src={"data:image/jpeg;base64,"}
                                                            style={{height: '65px'}}
                                                            alt={"employee excel upload"}/></Row>
                                                        <Row><span>Çalışan seçimini Excel'den yükleyerek yapmak için tıklayın</span></Row>
                                                    </div>}/>
                            </div>}
                        {this.state.selectedScreen === "setManager" && this.state.selection.length > 0 &&
                            <SetManagerModal
                                selection={this.props.selection.length > 0 ? this.props.selection : this.state.selection}
                                modal={this.state.selectedScreen === "setManager"}
                                saveClicked={this.state.saveClicked}
                                saveCompleted={() => this.setState({btnDisabled: false, saveClicked: false})}
                                closeModal={() => this.props.toggleModal(false, true)}/>
                        }
                        {this.state.selectedScreen === "updateStatus" && this.state.selection.length > 0 &&
                            <UpdateStatusModal
                                selection={this.props.selection.length > 0 ? this.props.selection : this.state.selection}
                                modal={this.state.selectedScreen === "updateStatus"}
                                saveClicked={this.state.saveClicked}
                                saveCompleted={() => this.setState({btnDisabled: false, saveClicked: false})}
                                closeModal={() => this.props.toggleModal(false, true)}/>
                        }
                        {this.state.selectedScreen === "twoStepVerification" && this.state.selection.length > 0 &&
                            <TwoStepVerificationModal
                                selection={this.props.selection.length > 0 ? this.props.selection : this.state.selection}
                                modal={this.state.selectedScreen === "twoStepVerification"}
                                saveClicked={this.state.saveClicked}
                                saveCompleted={() => this.setState({btnDisabled: false, saveClicked: false})}
                                closeModal={() => this.props.toggleModal(false, true)}/>
                        }
                        {this.state.selectedScreen === "addEmployeeToGroup" && this.state.showExcelImport && !(this.props.selection.length > 0) &&
                            <AddEmployeeToGroupModal
                                selection={this.props.selection.length > 0 ? this.props.selection : this.state.selection}
                                modal={this.state.selectedScreen === "addEmployeeToGroup"}
                                saveClicked={this.state.saveClicked}
                                saveCompleted={() => this.setState({btnDisabled: false, saveClicked: false})}
                                closeModal={() => this.props.toggleModal(false, true)}/>
                        }
                        {this.state.selectedScreen === "deleteEmployeeFromGroup" && this.state.showExcelImport && !(this.props.selection.length > 0) &&
                            <DeleteEmployeeFromGroupModal
                                selection={this.props.selection.length > 0 ? this.props.selection : this.state.selection}
                                modal={this.state.selectedScreen === "deleteEmployeeFromGroup"}
                                saveClicked={this.state.saveClicked}
                                saveCompleted={() => this.setState({btnDisabled: false, saveClicked: false})}
                                closeModal={() => this.props.toggleModal(false, true)}/>
                        }
                    </div>
                </Form>
                {this.state.alert}
            </div>
        </DefaultModal>
    }
}

export default EmployeeTaskScreens;
