import React, {Component} from "react";
import {Button, Col, Form, FormGroup, Input, Label, Modal, ModalBody, ModalFooter, ModalHeader} from "reactstrap";
import {requestK} from "../../../../helper/APIUtils";
import {showAxiosError} from "../../../../helper/helper";
import Swal from "sweetalert2";

class CreateEmployeeGroup extends Component {
    constructor(props) {
        super(props);
        this.state = {
            saving: false,
            group: props.selectedGroup !== undefined ? props.selectedGroup : {}
        }

        this.editHandleInputChange = this.editHandleInputChange.bind(this);
        this.saveClicked = this.saveClicked.bind(this);
        this.setGroup = this.setGroup.bind(this);
    }

    editHandleInputChange(event) {
        const target = event.target;
        let value = target.value;
        let group = this.state.group;
        group.name = value;
        this.setState({group});
    }

    setGroup(row) {
        this.setState({group: row});
    }

    saveClicked() {
        let self = this;
        let params = {
            url: '/employee/saveGroup',
            method: "post",
            data: this.state.group
        };
        this.setState({saving: true})
        requestK(params)
            .then(function (response) {
                self.setState({saving: false});
                self.props.closeModal();
                Swal({
                    text: 'Kaydetme işlemi başarıyla gerçekleşti.',
                    type: 'success',
                    confirmButtonText: 'Tamam'
                }).then((result) => {
                });
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({saving: false});
        });

    }

    render() {
        return <Modal isOpen={this.props.displayCreateUpdateGroupDialog} size="md">
            <ModalHeader className="justify-content-center"
                         toggle={() => this.props.closeModal()}>
                Çalışan Grubu Oluştur
            </ModalHeader>
            <ModalBody>
                <Form>
                    <FormGroup row>
                        <Label sm={4} for="groupName">Grup Adı: *</Label>
                        <Col sm={8}>
                            <Input type="text" name="groupName" value={this.state.group.name}
                                   onChange={this.editHandleInputChange}
                            />
                        </Col>
                    </FormGroup>
                </Form>
            </ModalBody>
            <ModalFooter>
                <Button title="İptal" style={{paddingLeft: '16px', width: '100px'}} color="secondary"
                        onClick={() => this.props.closeModal()}
                        disabled={this.state.saving}
                >
                    İptal
                </Button>
                <Button title="Kaydet" style={{paddingLeft: '16px', width: '100px'}} color="primary"
                        disabled={this.state.saving}
                        onClick={this.saveClicked}
                >
                    Kaydet
                </Button>
            </ModalFooter>
        </Modal>
    }

}

export default CreateEmployeeGroup;