import React, {Component} from 'react';
import {requestK} from "../../../../helper/APIUtils";
import {checkGrantToDisplay, showAxiosError} from "../../../../helper/helper";
import TextOverFlowTooltip from "../../../../components/TextOverFlowTooltip";
import Table from "../../../../components/Table/Table";
import DefaultModal from "../../../../components/DefaultModal";
import {EMPLOYEES} from "../../../../common/authorities/GrantDef";
import MiniButton from "../../../../components/MiniButton";
import SweetAlertExt from "../../../../components/SweetAlertExt";
import Alert from "react-s-alert";
import moment from "moment";

class VaccinationCardHistory extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            manual: false,
            content: [],
            total: 0,
            page: 0,
            size: 10,
            alert: null
        };
        this.fetchData = this.fetchData.bind(this);
    }

    hasGrant = (grant) => {
        return this.props.hasGrant(grant);
    };

    deleteRow(row) {
        let self = this;
        let params = {
            url: `/employeevaccination/deleteVaccinationCard`,
            method: "post",
            data: row.id
        };
        self.setState({deleteLoading: true, alert: null, loading: true});
        requestK(params)
            .then(function (response) {
                let message = response.data;
                self.setState({
                    selected: null,
                    alert: null,
                    manual: true,
                    page: 0,
                    deleteLoading: false,
                    loading: false,
                });
                Alert.success("Aşı kartı/PCR testi bilgisi silindi", {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 5000
                });
            })
            .catch(function (error) {
                showAxiosError(error);
                self.setState({deleteLoading: false, createUpdateLoading: false, loading: false});
            });
    }

    fetchData(accessor, direction, page, size) {
        let self = this;
        let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '&sort=id,desc';
        let params = {
            url: '/employeevaccination/readVaccinationCardHistoryList?page=' + page + '&size=' + size + sortStr,
            method: "post",
            data: self.props.selection
        };
        self.setState({loading: true, manual: self.manual === true ? false : self.manual});
        requestK(params)
            .then(function (response) {
                self.setState({
                    page: page,
                    size: size,
                    total: (response.data && response.data.page) ? response.data.page.totalElements : 0,
                    content: (response.data && response.data.page) ? response.data.page.content : [],
                    totalIdList: response.data ? response.data.selectedIdList : [],
                    loading: false,
                });
            }).catch(function (error) {
            self.setState({loading: false});
            showAxiosError(error);
        });
    }

    renderVaccinationListInfo = () => {
        let columns = [
            {
                Header: "Belge Türü",
                accessor: "vaccinationCards_cardType",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        <TextOverFlowTooltip text={row.cardType && row.cardType === "PCR" ? "PCR Testi": (row.cardType === "VACCINATION" ? "Aşı" : row.cardType)} maxLength={12}/>
                    </div>
                )
            },
            {
                Header: "Aşılanma Tarihi",
                accessor: "vaccinationCards_vaccinationDate",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        <TextOverFlowTooltip text={moment(row.vaccinationDate).isValid() ? moment(row.vaccinationDate).format("DD-MM-YYYY") : ''} maxLength={12}/>
                    </div>
                )
            },
            {
                Header: "Doz Numarası",
                accessor: "vaccinationCard_doseNumber",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        <TextOverFlowTooltip text={row.doseNumber} maxLength={12}/>
                    </div>
                )
            },
            {
                Header: "Aşı Üreticisi",
                accessor: "vaccinationCard_manufacturer",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        <TextOverFlowTooltip text={row.manufacturer} maxLength={12}/>
                    </div>
                )
            },
            {
                Header: "Sertifika ID",
                accessor: "vaccinationCard_certificateId",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        <TextOverFlowTooltip text={row.certificateId} maxLength={12}/>
                    </div>
                )
            },
            {
                Header: "Test Açıklanma Tarihi",
                accessor: "vaccinationCards_resultDate",
                sortable: false,
                Cell: row => (
                    <div style={{margin: 0}}>
                        <TextOverFlowTooltip text={moment(row.resultDate).isValid() ? moment(row.resultDate).format("DD-MM-YYYY HH:mm:ss") : ''} maxLength={12}/>
                    </div>
                )
            },
            {
                Header: "İşlemler",
                accessor: "",
                sortable: false,
                Cell: row => {
                    return (
                        <div style={{textAlign: 'left', WebkitTextFillColor: 'white'}}>
                            {checkGrantToDisplay(this, EMPLOYEES) &&
                            <MiniButton title="Sil" faIcon="fa-trash" color="danger"
                                        onClick={() => {
                                            this.setState({
                                                alert: (
                                                    <SweetAlertExt showCancel warning
                                                                   confirmBtnText="Evet"
                                                                   cancelBtnText="Hayır"
                                                                   confirmBtnBsStyle="danger"
                                                                   cancelBtnBsStyle="default"
                                                                   title="Emin misiniz?"
                                                                   onConfirm={() => this.deleteRow(row)}
                                                                   onCancel={() => this.setState({alert: null})}
                                                                   loading={this.state.deleteLoading}
                                                    >
                                                        {"Seçilen aşı kartı/PCR testi bilgisi silinecek."}
                                                    </SweetAlertExt>
                                                )
                                            });
                                        }}/>}
                        </div>
                    )
                },
                width: 'auto'
            }
        ];

        return (
            <div className="row col-sm-12" style={{display: 'inline-block'}}>
                <div style={{padding: '10px'}}>
                    <Table columns={columns}
                           content={this.state.content}
                           loading={this.state.loading}
                           total={this.state.total}
                           page={this.state.page}
                           manual={this.state.manual}
                           size={this.state.size}
                           options={["5", "10", "20", "50", "100"]}
                           fetchData={this.fetchData}/>
                </div>
            </div>
        );
    }

    render() {
        return <DefaultModal
            disabled={this.state.btnDisabled}
            isOpen={this.props.modal}
            size="lg"
            title="Aşı Kartı/PCR Testi Geçmişi"
            cancelBtnText="Kapat"
            onCancel={() => this.props.toggleModal(false, true)}>
            <div style={{marginTop: '20px'}}>
                {this.renderVaccinationListInfo()}
                {this.state.alert}
            </div>
        </DefaultModal>
    }
}

export default VaccinationCardHistory;